import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent{
  title = 'Codestore';
  dropDown = false;
  presentValue: number;
  actualValue: number;
  depreciated: number;
  addOns:number;
  yearofpurchase = [];
  CarAge: number;
  disabledSubmit:boolean =true ;
  years:string = 'Select Year';
  policy = [
    {value: '0', viewValue: 'Select an option'},
    {value: '1', viewValue: 'Not yet expired'},
    {value: '2', viewValue: 'Expired for more than 90 days'},
    {value: '3', viewValue: 'Expired for less than 90 days'}
  ];
  diceFaces = [
    {value: '1', img: 'https://upload.wikimedia.org/wikipedia/commons/2/2c/Alea_1.png'},
    {value: '2', img: 'https://upload.wikimedia.org/wikipedia/commons/b/b8/Alea_2.png'},
    {value: '3', img: 'https://upload.wikimedia.org/wikipedia/commons/2/2f/Alea_3.png'},
    {value: '4', img: 'https://upload.wikimedia.org/wikipedia/commons/8/8d/Alea_4.png'},
    {value: '5', img: 'https://upload.wikimedia.org/wikipedia/commons/5/55/Alea_5.png'},
    {value: '6', img: 'https://upload.wikimedia.org/wikipedia/commons/f/f4/Alea_6.png'}
  ]
  diceRolled =  [
    {value: '1', img: 'https://upload.wikimedia.org/wikipedia/commons/2/2c/Alea_1.png'},
    {value: '2', img: 'https://upload.wikimedia.org/wikipedia/commons/b/b8/Alea_2.png'},
    {value: '3', img: 'https://upload.wikimedia.org/wikipedia/commons/2/2f/Alea_3.png'},
    {value: '4', img: 'https://upload.wikimedia.org/wikipedia/commons/8/8d/Alea_4.png'},
    {value: '5', img: 'https://upload.wikimedia.org/wikipedia/commons/5/55/Alea_5.png'}
  ]
  IDV: number;
  addOn: number;
  postSubmit: boolean = false;
  DiceRollFlag: boolean;
  rolledSum: number;
  currentpolicy: any ;
  ODC: any;
  carNumber: any;
  ngOnInit(){
    this.currentpolicy = 0;
    this.yearofpurchase[0] = 'Select Year'
    for(let i=1; i<21;i++)
    this.yearofpurchase[i] = 2000 + i;
  }

  ODCover(){
    this.DiceRollFlag = false;
    this.CarAge = 2020 - Number(this.years);
    this.addOn = Number(this.addOns)? Number(this.addOns):0;
    this.presentValue = Number(this.actualValue) * Math.pow(0.86, this.CarAge); //@ 14% depreciation rate per annum (1-rate)
    this.depreciated = Number(this.actualValue) - this.presentValue;
    this.IDV =  Number(this.actualValue) + this.addOn - this.depreciated; //Insurance Declared Value
    // this.IDV = Math.round(this.IDV);
    this.ODC = this.IDV * 0.085;
    this.presentValue = Math.round(this.presentValue);
    this.depreciated = Math.round(this.depreciated);
    this.ODC = Math.round(this.ODC);
    if(this.IDV){
      this.postSubmit=true;
      if(this.CarAge == 0){
        this.years = 'Brand New';
      }
     else{
       this.years = this.CarAge+ ' Years';
     }
    }
    else{
      this.postSubmit=false;
    }
    console.log(this.CarAge, this.presentValue, this.actualValue,this.depreciated, this.IDV)
  }

  ValidateFields(){
    this.CarAge = 2020 - Number(this.years);
    this.presentValue = Number(this.actualValue) * Math.pow(0.86, this.CarAge)
    if(!this.presentValue || !this.currentpolicy){
      this.disabledSubmit = true
    }
    else{
      this.disabledSubmit = false
    }
  }
  DiceInit(){
    this.postSubmit = true;
    this.DiceRollFlag = true;
    this.rolledSum = 0;
  }

  SingleDice(val, index)
  {
    var rolled=0;
    console.log(val, index)
    rolled = Math.floor((Math.random() * 6) + 1);
    this.diceRolled[index] = this.diceFaces[rolled-1];
    this.SumofRoll();
  }
  RollDice(){
    this.diceRolled = [];
    var rolled = 0;
    for(let i=0; i<5; i++){
      rolled = Math.floor((Math.random() * 6) + 1);
      this.diceRolled.push(this.diceFaces[rolled-1])
    }
    this.SumofRoll();
    console.log(this.diceRolled);
  }

  SumofRoll(){
    this.rolledSum = 0;
    for(let temp of this.diceRolled){
      this.rolledSum = this.rolledSum + Number(temp.value);
    }
  }

  back(){
    this.postSubmit=false; 
    this.years = 'Select Year'; 
    this.disabledSubmit = true;
    this.carNumber= null;
    this.actualValue = null;
    this.addOns =null;
    this.currentpolicy = 0;
  }

}
